body {
	font-family: $main-font;
	font-size: 14px;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
	margin: 0;
	padding: 0;
}

img {
	display: block;
	max-width: 100%;
	height: auto;
}

button {
	padding: 0;
	border: 0;
	background-color: transparent;
	cursor: pointer;
	font-family: inherit;
}

/* Utils */

.no-scroll {
	overflow: hidden;
}

/* Hidden */

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}
